<template>
  <div class="row">
    <div
      class="col-sm-12 col-md-6 col-lg-4 col-xl-3"
      v-for="(post, key) in getPostList[0]"
      :key="key"
    >
      <div class="card p-3">
        <router-link tag="a" :to="{name: 'post', params: {id: post.id}}">
          <h3>{{ post.userId }}</h3>
          <hr />
          <h4>{{ post.title }}</h4>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["getPostList"]),
  },
  created() {
    this.$store.dispatch("getPosts");

  }
};
</script> 