<template>
  <div class="container mt-5">
    <router-link tag="button" class="btn btn-secondary text-white mb-3" to="/"
      >Back To Home</router-link
    >
    <hr class="mb-3" />
    <div class="card p-3">
      <h1>{{ getPostInner.title }}</h1>
      <p>{{ getPostInner.id }}</p>
      <p>{{ getPostInner.userId }}</p>
      <p>{{ getPostInner.body }}</p>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["getPostInner"]),
  },
  mounted() {
    this.$store.dispatch("getPost", this.$route.params.id);
  },
};
</script>