import { createStore } from 'vuex'
import axios from 'axios'
export default createStore({
  state: {
    posts: [],
    post: {}
  },
  getters: {
    getPostList(state) {
      return state.posts;
    },
    getPostInner(state) {
      return state.post;
    }
  },
  mutations: {
    pushPosts(state, posts) {
      state.posts.push(posts);
    },
    pushPost(state, post) {
      state.post = post;
    }
  },
  actions: {
    getPosts({ commit }) {
      axios.get("https://jsonplaceholder.typicode.com/posts")
        .then(response => {
          commit("pushPosts", Object.freeze(response.data))
        })
    },
    getPost({commit}, post) {
      axios.get(`https://jsonplaceholder.typicode.com/posts/${post}`)
        .then(response => {
          commit("pushPost", Object.freeze(response.data))
        })
    }
  }
})
